import React, {useState} from 'react';
import {Button, Col, Container, Form, Row} from "react-bootstrap";
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import Calendar from "./Calendar";

const TaskView = (props) => {
    const [nameTask, setNameTask] = useState('');

    const options = [
        'No priority', 'Priority 1', 'Priority 2', 'Priority 3', 'Priority 4'
    ];
    const defaultOption = options[0];

    const handleChange = (event) => {
        setNameTask(event.target.value);
    };

    return (
        <Container className="mb-5">
            <Form>
                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                    <Form.Control type="text" value={nameTask} placeholder="Task's name" name="nameTask" onChange={handleChange}/>
                    <Form.Control as="textarea" rows={3} placeholder="Task's description" name="description" />
                    <Row>
                        <Col className='mt-2' sm={12} lg={6} ><Dropdown options={options} value={defaultOption} placeholder="Select an option"/></Col>
                        <Col className='mt-3'  sm={12} lg={6}>
                            <Calendar></Calendar>
                        </Col>
                    </Row>
                </Form.Group>
            </Form>

            <Row className="mt-2">
                <Col xs={6} sm={2} lg={1} ><Button variant="dark" disabled={!nameTask} size="sm">ADD</Button></Col>
                <Col xs={6} sm={2} lg={1} ><Button variant="dark" size="sm" onClick={() => {
                    props.func()
                }}>CANCEL</Button></Col>
            </Row>
        </Container>
    );
};
export default TaskView